@import "../base/variables";

.column {
  padding: 0 1.875rem 3.125rem;
  width: 100%;

  @media #{$mediumUp} {
    border: 1px solid $gray;
    margin: 3.125rem 1.875rem;
    width: 50%;
  }

  h1 {
    font-size: 6.875rem;
    font-weight: bold;
    margin: 1.875rem auto;
    text-align: center;
  }

  p {
    margin-bottom: 1.5rem;
  }
}
